




















































import uniIcon from '@/components/uni-icon.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
var cate, userLoginInfo;
export default {
	data() {
		return {
			listData: [],
			last_id: "",
			reload: false,
			headerPosition: "fixed",
			status: 0,
			category: [{
					status: 0,
					name: "全部"
				},
				{
					status: 1,
					name: "待付款"
				},
				{
					status: 2,
					name: "待发货"
				},
				{
					status: 3,
					name: "待收货"
				},
				{
					status: 4,
					name: "已完成"
				}
			],
			cateCurrentIndex: 0,
			orderGoodsList: [],
			goodss: [],
			sum: 0,
			// allPrice:0,
			page: 1,
			loadingType: 1,
		}

	},
	components: {
		//引用mSearch组件，如不需要删除即可
		uniIcon,
		uniLoadMore
	},
	onLoad(params) {
		if (params.status) {
			this.status = params.status;
		}
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../order/order', 1);
		this.cateCurrentIndex = this.status;
		this.orderGoodsList = [];
		this.loadingType = 1;
		this.page = 1;
		this.getOrderList();
		//console.log(userLoginInfo);
	},
	onPullDownRefresh() {},
	//页面上拉触底事件的处理函数
	onReachBottom() {
		//console.log("onReachBottom");
		this.getOrderList();
	},
	onHide(){
		// console.log("页面隐藏了");
	},
	methods: {
		navigateBack() {
			uni.navigateBack();
		},
		// 根据status 加载商品状态  ---待收货--代发货
		getOrderList() {
			uni.request({
				url: this.apiUrl + 'user/getOrder',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					page: this.page,
					status: this.status
				},
				success: (res) => {
					if (res.data.code == 0) {
						if (Array.isArray(res.data.data) && res.data.data.length === 0) {
							this.loadingType = 2;
							return;
						}
						if (res.data.data.length < 6) {
							this.loadingType = 2;
						}
						
						this.page++;
						let orderData = [];
						res.data.data.forEach((orders) => {
							//获取商品总件数
							orders.sum = 0;
							// orders.allPrice = 0;
							orders.goods.forEach((goods) => {
								orders.sum += goods.num;
								// orders.allPrice += goods.price;
							})
							orderData.push(orders);
						});
						this.orderGoodsList = this.orderGoodsList.concat(orderData);
						// 拼接加载更多数据
						// console.log(this.orderGoodsList);
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		},
		// 切换tabbar
		tabChange: function(e) {
			this.loadingType = 1;
			var status = e.currentTarget.dataset.status;
			var index = e.currentTarget.dataset.index;
			this.cateCurrentIndex = index;
			this.status = status;
			// console.log(this.status);
			this.orderGoodsList = [];
			this.page = 1;
			this.getOrderList();
		},
		doSearch() {
			uni.navigateTo({
				url: '../search/search'
			})
		},
		// 			onLoad(event) {
		// 				// 目前在某些平台参数会被主动 decode，暂时这样处理。
		// 				try {
		// 					this.banner = JSON.parse(decodeURIComponent(event.detailDate));
		// 				} catch (error) {
		// 					this.banner = JSON.parse(event.detailDate);
		// 				}
		// 				uni.setNavigationBarTitle({
		// 					title: this.banner.title
		// 				});
		// 			},
		// 跳转到商品详情页
		goOrderDetail(trade) {
			uni.navigateTo({
				// url: './adress?editId=' + addressId
				url: './receive_goods?order_trade_no=' + trade
			})
		},
		// 去评论
		evaluate(goodsId, goodsImage) {
			uni.navigateTo({
				url: '../goods/evaluate?goods_id=' + goodsId + '&goods_image=' + goodsImage
			})
		},
		// 去付款
		goPay(pays) {
			uni.redirectTo({
				url: '../pay/pay?order_trade_no=' + pays
			})
		},
		// 删除订单
		delectOrder(trade) {
			uni.request({
				url: this.apiUrl + 'user/setOrderStatus',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					order_trade_no: trade,
					status: -1
				},
				success: (res) => {
					if (res.data.code == 0) {
						uni.showToast({
							title: '删除成功！',
							icon: 'none',
							success: () => {
								// 清空商品信息
								this.orderGoodsList = [];
								this.page = 1;
								this.getOrderList();
							}
						})

					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		},
		// 确认收货
		conformOrder(trade) {
			uni.request({
				url: this.apiUrl + 'user/setOrderStatus',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					order_trade_no: trade,
					status: 1
				},
				success: (res) => {
					if (res.data.code == 0) {
						uni.showToast({
							title: '已确认收货',
							icon: 'none',
							success: () => {
								// 清空商品信息
								this.orderGoodsList = [];
								this.page = 1;
								this.getOrderList();
							}
						})
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		},
	},
}
